import React, { useState } from "react";
import PizZip from "pizzip";
import Docxtemplater from "docxtemplater";
import { saveAs } from "file-saver";

const CreateZayavkaForm = () => {
  const [formData, setFormData] = useState({
    perevozchik: { name: "", address: "", inn: "", phone: "" },
    platelshik: {
      name: " .",
      address:
        "620085,  , . ,  8 , 212, . 335, : 8-800-234-59-60",
      inn: " 6679113421;  667901001",
      phone: "8-800-234-59-60",
    },
    gruzootpravitel: { name: "", address: "", inn: "", contact: "" },
    gruzopoluchatel: { name: "", address: "", contact: "" },
    gruz: { weight: "", description: "" },
    transport: {
      type: "",
      loadingType: "",
      unloadingType: "",
      docCountLoad: "",
      docCountUnload: "",
    },
    driver: { name: "", passport: "", vehicleInfo: "" },
    details: { dateTime: "", duration: "", notes: "", route: "", cost: "" },
  });

  const handleChange = (section, field, value) => {
    setFormData((prev) => ({
      ...prev,
      [section]: {
        ...prev[section],
        [field]: value,
      },
    }));
  };

  const Input = ({ section, field, placeholder }) => {
    const value = formData?.[section]?.[field] ?? "";

    return (
      <input
        type="text"
        placeholder={placeholder}
        className="input border p-2 rounded w-full"
        value={value}
        onChange={(e) => handleChange(section, field, e.target.value)}
      />
    );
  };

  const handleDownload = async () => {
    try {
      const response = await fetch("/template.docx");
      const content = await response.arrayBuffer();

      const zip = new PizZip(content);
      const doc = new Docxtemplater(zip, {
        paragraphLoop: true,
        linebreaks: true,
      });

      doc.setData(formData);
      doc.render();

      const out = doc.getZip().generate({ type: "blob" });
      saveAs(out, "zayavka.docx");
    } catch (error) {
      console.error("  DOCX:", error);
      alert("    .");
    }
  };

  return (
    <div id="zayavka-form">
      <form
        onSubmit={(e) => e.preventDefault()}
        className="space-y-8 max-w-3xl mx-auto"
      >
        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input
          section="perevozchik"
          field="name"
          placeholder=" "
        />
        <Input
          section="perevozchik"
          field="address"
          placeholder=" "
        />
        <Input
          section="perevozchik"
          field="inn"
          placeholder="/ "
        />
        <Input
          section="perevozchik"
          field="phone"
          placeholder=" "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input
          section="gruzootpravitel"
          field="name"
          placeholder=" "
        />
        <Input
          section="gruzootpravitel"
          field="address"
          placeholder=" "
        />
        <Input
          section="gruzootpravitel"
          field="inn"
          placeholder="/ "
        />
        <Input
          section="gruzootpravitel"
          field="contact"
          placeholder=" "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input
          section="gruzopoluchatel"
          field="name"
          placeholder=" "
        />
        <Input
          section="gruzopoluchatel"
          field="address"
          placeholder=" "
        />
        <Input
          section="gruzopoluchatel"
          field="contact"
          placeholder=" "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input section="gruz" field="weight" placeholder="  ()" />
        <Input
          section="gruz"
          field="description"
          placeholder=" "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input section="transport" field="type" placeholder=" " />
        <Input
          section="transport"
          field="loadingType"
          placeholder=" "
        />
        <Input
          section="transport"
          field="unloadingType"
          placeholder=" "
        />
        <Input
          section="transport"
          field="docCountLoad"
          placeholder="-   "
        />
        <Input
          section="transport"
          field="docCountUnload"
          placeholder="-   "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input section="driver" field="name" placeholder=" " />
        <Input
          section="driver"
          field="passport"
          placeholder=" "
        />
        <Input
          section="driver"
          field="vehicleInfo"
          placeholder=" "
        />

        <h2 className="text-xl font-bold mb-4 text-gray-700 dark:text-white">
          
        </h2>
        <Input
          section="details"
          field="dateTime"
          placeholder="   "
        />
        <Input section="details" field="duration" placeholder="  " />
        <Input section="details" field="route" placeholder="" />
        <Input section="details" field="cost" placeholder="" />
        <Input
          section="details"
          field="notes"
          placeholder=" "
        />

        <button
          type="button"
          onClick={handleDownload}
          className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
            DOCX
        </button>
      </form>
    </div>
  );
};

export default CreateZayavkaForm;